*{
    margin: 0;
    padding: 0;
}
body{
    scroll-behavior: smooth;
   background: -webkit-gradient(linear, left top, right top, from(rgba(183,222,237,1)), color-stop(50%, rgba(113,206,239,1)), color-stop(51%, rgba(33,180,226,1)), to(rgba(183,222,237,1)));
   background: linear-gradient(to right, rgba(183,222,237,1) 0%, rgba(113,206,239,1) 50%, rgba(33,180,226,1) 51%, rgba(183,222,237,1) 100%);
}
@-webkit-keyframes sc{
    0%{
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    100%{
        -webkit-transform: translateX(-282vw);
        transform: translateX(-282vw);
    }
}
@keyframes sc{
    0%{
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    100%{
        -webkit-transform: translateX(-280vw);
        transform: translateX(-280vw);
    }
}
#banner{
    position: relative;
    width: 80vw;
    overflow: hidden;
    margin: 0 auto;
}

#banner::before{
    position: absolute;
    content: '';
    top: 75px;
    left: 0;
    height: 100%;
    width: 30px;
    
    background: -webkit-gradient(linear,right top, left top,from(transparent),to(white));
    
    background: linear-gradient(to left,transparent,white);
    z-index: 10;
}
#banner::after{
    position: absolute;
    content: '';
    top: 75px;
    right: 0;
    height: 100%;
    width: 30px;
    background: -webkit-gradient(linear,left top, right top,from(transparent),to(rgba(255,255,255,0.8)));
    background: linear-gradient(to right,transparent,rgba(255,255,255,0.8));
    z-index: 10;
}
#mar>li>img{
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear; 
}
#mar>li>img:hover{
    
    -webkit-transform: scale(0.95);
    
    transform: scale(0.95);
    z-index: 100;
}
#container{
/*    height: 100vh;*/
    width: 95vw;
    margin: 0 auto;
    background: rgba(215,150,111,0.5);
    margin-top: 20px;
    opacity: 0;
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
    overflow: hidden;
}
@-webkit-keyframes n{
    0%{
        opacity: 0;
       -webkit-transform: translateX(-100px);
       transform: translateX(-100px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}
@keyframes n{
     0%{
        opacity: 0;
       -webkit-transform: translateX(-100px);
       transform: translateX(-100px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}
@-webkit-keyframes alt{
     0%{
        opacity: 0;
       -webkit-transform: translateX(-100px);
       transform: translateX(-100px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}
@keyframes alt{
     0%{
        opacity: 0;
       -webkit-transform: translateX(-100px)scale(1);
       transform: translateX(-100px)scale(1);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateX(0)scale(1);
        transform: translateX(0)scale(1);
    }
}
.singleComponet{
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
}
.singleComponet:hover{
    -webkit-transform: scale(1.01);
    transform: scale(1.01);
}